import { HashRouter as Router, Route, Routes } from 'react-router-dom';
import { App } from './App';
// eslint-disable-next-line max-len
import { ScrollToTop } from './utils/ScrollToTop';
import { Suspense, lazy } from 'react';
import { PageLoader } from './components/PageLoader/PageLoader';

const HomePage = lazy(() => import('./pages/HomePage/HomePage'));
const CatalogPage = lazy(() => import('./pages/CatalogPage/CatalogPage'));
const FavoritesPage = lazy(() => import('./pages/FavoritesPage/FavoritesPage'));
const FaqPage = lazy(() => import('./pages/FaqPage/FaqPage'));
const NotFoundPage = lazy(() => import('./pages/NotFoundPage/NotFoundPage'));
const VendorDetailsPage = lazy(
  () => import('./pages/VendorDetailsPage/VendorDetailsPage'),
);
const VendorsPage = lazy(() => import('./pages/VendorsPage/VendorsPage'));
const ProductDetailsPage = lazy(
  () => import('./pages/ProductDetailsPage/ProductDetailsPage'),
);

const About = lazy(() => import('./pages/About/About'));

export const Root: React.FC = () => {
  return (
    <Router>
      <ScrollToTop />
      <Suspense fallback={<PageLoader />}>
        <Routes>
          <Route path="/" element={<App />}>
            <Route index element={<HomePage />} />
            <Route path="catalog">
              <Route index element={<CatalogPage />} />
              <Route path=":productId" element={<ProductDetailsPage />} />
            </Route>
            <Route path="vendorslist">
              <Route index element={<NotFoundPage />} />
              <Route path=":vendorId" element={<VendorDetailsPage />} />
            </Route>

            <Route path="vendors" element={<VendorsPage />} />
            <Route path="about" element={<About />} />
            <Route path="faq" element={<FaqPage />} />
            <Route path="favorites" element={<FavoritesPage />} />
            <Route path="*" element={<NotFoundPage />} />
          </Route>
        </Routes>
      </Suspense>
    </Router>
  );
};
